/*==========================================================
Default Styles
===========================================================*/
* {
    margin: 0;
    padding: 0;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

html {
  font-size: $g-font-body-ratio + %;
}

body {
  box-sizing: border-box;
  font-weight: normal;
  font-family: $g-font-primary;
  @include fz();
  line-height: 1.6;
  color: $g-font-color;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -moz-font-feature-settings: "liga", "kern";
  text-rendering: optimizelegibility;
  background-color: $g-bg-color;
  word-wrap: break-word;
  //overflow: hidden;
  // @include bp(lg) {
  //   @include rem("margin-left", 20);
  //   @include rem("margin-right", 20);
  // }
  // @include bp() {
  //   @include rem("padding-left", 20);
  //   @include rem("padding-right", 20);
  // }
  // @include bp(sm) {
  //   @include rem("padding-left", 20);
  //   @include rem("padding-right", 20);
  // }
}


/*==========================================================
Headings
===========================================================*/

h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
}

.h1 {@include fz($g-font-body-size * 2.5);}
.h2 {@include fz($g-font-body-size * 2);}
.h3 {@include fz($g-font-body-size * 1.75);}
.h4 {@include fz($g-font-body-size * 1.5);}
.h5 {@include fz($g-font-body-size * 1.25);}
.h6, p {@include fz();}
p { line-height: 1.8;}

/*==========================================================
Link
===========================================================*/

a {
  color: $g-link-color;
  text-decoration: none;

  &:hover {
    color: $g-link-color-hover;
  }

  &:focus {
    outline: none;
  }
}


/*==========================================================
List
===========================================================*/
ul, ol {
  list-style: none;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5rem;
}

dt {
	font-weight: bold;
}

/*==========================================================
Images
===========================================================*/

figure {
  margin-bottom: 0;
  line-height: 0;//bluma
}
img {
	height: auto;
	/* Make sure images are scaled correctly. */
	width: 100%;
	/* Adhere to container width. */
}

